import ajax from "@/utils/ajax"

// 用户
export function getUserList(param, pagination) {
  return ajax({
    url: "/api/manage/usermanage/getPageList",
    method: "get",
    params: { ...param, ...pagination },
  })
}

export function createUser(data) {
  return ajax({
    url: "/api/manage/usermanage/createUser",
    method: "post",
    data: data,
  })
}

export function updateUser(data) {
  return ajax({
    url: "/api/manage/usermanage/updateUser",
    method: "post",
    data: data,
  })
}

export function deleteUser(ids) {
  return ajax({
    url: "/api/manage/usermanage/deleteUser",
    method: "post",
    data: ids,
    headers: { "Content-Type": "application/json" },
  })
}

