<template>
  <div class="app-container">
    <!-- ////////// 目录模块 ////////// -->
    <div v-show="!inReader" class="excerpt-list">
      <div class="excerpt-list-container">
        <div class="filter-container">
          <!-- 字段搜索 -->
          <span style="font-size: 14px; font-weight: bold; vertical-align: 25%; color: gray"
            >关键字：</span
          >
          <template v-for="(item, index) in listQueryOptions" :key="index">
            <!-- 文本输入 -->
            <el-input
              v-if="!item.type || item.type == 'input'"
              v-model="listQueryParam[item.field]"
              :placeholder="item.label"
              clearable
              :style="'width: ' + item.width + '; margin-right: 10px'"
              class="filter-item"
              @keyup.enter="handleFilter"
            />
            <!-- 下拉选择 -->
            <el-select
              v-if="item.type == 'select'"
              v-model="listQueryParam[item.field]"
              :placeholder="item.label"
              clearable
              :style="'width: ' + item.width + '; margin-right: 10px'"
              class="filter-item"
              @change="handleFilter"
            >
              <el-option
                v-for="v in item.selectOptions"
                :key="v.value"
                :label="v.label"
                :value="v.value"
              />
            </el-select>
          </template>
          <span style="font-size: 14px; font-weight: bold; vertical-align: 25%; color: gray"
            >排序：</span
          >
          <!-- 排序方式 -->
          <el-select
            v-model="listPagination.sortType"
            placeholder="SortType"
            style="width: 100px; margin-right: 10px"
            class="filter-item"
            @change="handleFilter"
          >
            <el-option
              v-for="item in sortTypeOptions"
              :key="item"
              :label="item == 'asc' ? '顺序' : '逆序'"
              :value="item"
            />
          </el-select>
          <!-- 操作按钮 -->
          <el-button
            class="filter-item"
            style="margin-right: 20px"
            type="primary"
            icon="Search"
            @click="handleFilter"
          >
            查询
          </el-button>
          <el-checkbox
            v-if="expandColumns.length"
            v-model="expandAll"
            class="filter-item"
            @change="tableKey = tableKey + 1"
          >
            展开所有行
          </el-checkbox>
        </div>
        <!-- ////////// 表格 ////////// -->
        <el-table
          :key="tableKey"
          v-loading="listLoading"
          :data="list"
          border
          :default-expand-all="expandAll"
          table-layout="fixed"
          :max-height="1000"
          style="width: 800px"
          @sort-change="sortChange"
        >
          <!-- 展开标识 -->
          <el-table-column v-if="expandColumns.length" width="36px" type="expand" fixed="left">
            <template #default="props">
              <div style="margin-left: 86px; margin-bottom: 10px">
                <!-- <span v-for="(item, index) in expandColumns" :key="index"
              >{{ item.label }}: {{ props.row[item.field] }}</span
            > -->
                <el-table :data="[props.row]" :border="false">
                  <el-table-column
                    v-for="(item, index) in expandColumns"
                    :key="index"
                    :prop="item.field"
                    :label="item.label"
                    :width="item.width"
                    :formatter="item.formatter"
                    align="left"
                  >
                    <template v-slot="{ row }">
                      <!-- rate -->
                      <el-rate
                        v-if="item.cellType == 'rate'"
                        v-model="row[item.field]"
                        :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
                        :max="5"
                        style="margin-top: 8px"
                        :disabled="true"
                      />
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </template>
          </el-table-column>
          <!-- 表格动态列 -->
          <el-table-column
            v-for="(item, index) in initialColumns"
            :key="index"
            :prop="item.field"
            :label="item.label"
            :width="item.width"
            :min-width="item.minWidth"
            :formatter="item.formatter"
            align="left"
          >
            <template v-slot="{ row }">
              <span v-if="item.cellType == 'link'" :class="item.class" @click="ReadArticle(row)">
                {{ row[item.field] }}
              </span>
              <!-- rate -->
              <el-rate
                v-if="item.cellType == 'rate'"
                v-model="row[item.field]"
                :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
                :max="5"
                style="margin-top: 8px"
                :disabled="true"
            /></template>
          </el-table-column>
          <!-- 表格操作列 -->
          <el-table-column
            label="操作"
            align="center"
            width="120"
            class-name="small-padding fixed-width"
          >
            <template v-slot="{ row }">
              <el-button type="primary" size="small" @click="ReadArticle(row)"> 阅读 </el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- ////////// 底部分页栏 ////////// -->
        <pagination
          v-show="total > 0"
          :total="total"
          v-model:page="listPagination.pageIndex"
          v-model:limit="listPagination.pageSize"
          @pagination="getList"
        />
      </div>
    </div>

    <!-- ////////// 阅读模块 ////////// -->
    <YunReader
      v-show="inReader"
      :changeMode="changeMode"
      :inReader="inReader"
      :bookId="encode(bookName)"
      :bookName="bookName"
      :articleData="articleData"
      :chapterList="chapterList"
    />
  </div>
</template>

<script>
import { getArticleExcerptList } from "@/api/manage"
import Pagination from "@/components/Pagination"
import YunReader from "@/components/YunReader/YunReader.vue"

export default {
  name: "ExcerptList",
  components: { Pagination, YunReader },
  data() {
    return {
      // 搜索栏配置
      listQueryOptions: {},
      // 搜索条件
      listQueryParam: {},
      // 分页条件
      listPagination: {
        sort: "id", // 默认按id排序（首字母不要大写）
        sortType: "desc",
        pageIndex: 1,
        pageSize: 20,
      },
      tableKey: 0,
      list: null, //表格数据
      total: 0, //总行数
      listLoading: false,
      articleData: "",
      bookName: "",
      chapterList: [],
      inReader: false,
      expandAll: false,
      sortTypeOptions: ["asc", "desc"],
      // 表头数据
      tableColumns: [
        {
          field: "reference",
          label: "作品名",
          minWidth: "120px",
          queryable: true,
          queryBoxWidth: "160px",
          queryBoxType: "input",
        },
        {
          field: "themes",
          label: "主题",
          minWidth: "140px",
          cellType: "link",
          class: "link-type",
        },
        { field: "tags", label: "标签", minWidth: "150px" },
        { field: "author", label: "作者", width: "120px", expand: "true" },
        { field: "grade", label: "评分", width: "150px", cellType: "rate", expand: "true" },
        {
          field: "copyright",
          label: "版权",
          width: "100px",
          expand: "true",
          formatter: this.copyrightFormatter,
        },
      ],
    }
  },
  created() {
    // 根据表格列信息初始化搜索字段配置
    this.listQueryOptions = this.tableColumns
      .filter((item) => {
        return item.queryable
      })
      .map((item) => {
        this.listQueryParam[item.field] = undefined
        return {
          field: item.field,
          label: item.label,
          width:
            item.queryBoxWidth.length > 0
              ? item.queryBoxWidth
              : item.width.length > 0
              ? item.width
              : item.minWidth,
          type: item.queryBoxType,
          selectOptions: item.selectOptions,
        }
      })
    // 获取列表
    this.getList()
  },
  methods: {
    //字符串转base64
    encode(str) {
      let encode = encodeURI(str) // 对字符串进行编码
      let base64 = btoa(encode) // 对编码的字符串转化base64
      return base64
    },
    getList() {
      this.listLoading = true
      getArticleExcerptList(this.listQueryParam, this.listPagination).then((response) => {
        this.list = response.data.data
        this.total = response.data.total
        this.listLoading = false
      })
    },
    handleFilter() {
      this.listPagination.pageIndex = 1
      this.getList()
    },
    //阅读文章
    ReadArticle(row) {
      this.bookName = row.reference
      //后端字符串如果用\r\n换行，前端编辑后换行会变成\n，所以统一用\n
      const textArr = row.text.replaceAll("\r\n", "\n").replaceAll("\r", "\n").split("\n")
      this.articleData = { title: row.themes, content: textArr }
      this.chapterList = [this.articleData]
      this.inReader = !this.inReader
    },
    changeMode() {
      this.inReader = !this.inReader
    },
    sortChange(data) {
      const { prop, order } = data
      this.listPagination.sort = prop
      this.listPagination.sortType = order === "ascending" ? " asc " : " desc "
      this.handleFilter()
    },
    /* ****** formatter ****** */
    //全部参数:(row, column, cellValue, index)
    //性别
    copyrightFormatter(row) {
      return row.copyright == 0 ? "摘抄" : row.copyright == 1 ? "原创" : "改编"
    },
  },
  computed: {
    // 过滤出最初展示的列
    initialColumns: function () {
      return this.tableColumns.filter((column) => {
        return !column.expand
      })
    },
    // 过滤出要折叠的列
    expandColumns: function () {
      return this.tableColumns.filter((column) => {
        return column.expand
      })
    },
  },
}
</script>

<style lang="scss">
.app-container {
  padding: 20px 0;
  .excerpt-list {
    overflow-x: auto;
    .excerpt-list-container {
      width: 840px;
      margin: 0 auto;
    }
  }
}
</style>
